import React from 'react';
import Image from 'next/image';

const galleryImages = [
  "gallery-thu.png",
  "gallery-thu2.png",
  "gallery-thu3.png",
  "gallery-thu4.png",
  "gallery-thu5.png",
  "gallery-thu6.png",
  "gallery-thu7.png",
  "gallery-thu8.png",
];

const GalleryArea = () => {
  return (
    <div className="gallery-area">
      <div className="container">
        <div className="row">
          <div className="col-lg-12">
            <div className="section-title text-center cursor-scale">
              <div className="section-sub-title">
                <h5>Gallery of Love</h5>
              </div>
              <div className="section-main-title cursor-scale">
                <h1>Picture Perfect Moments</h1>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="gallery__wrapper">
        <div className="container-fluid">
          <div className="row">
            {galleryImages.map((img, idx) => (
              <div className="col-xl-3 col-lg-4 col-md-6" key={img}>
                <div className="gallery__thumb-box">
                  <div className="gallery__thumb">
                    <a
                      className="popup-image"
                      href={`/assets/images/home-1/${img}`}
                    >
                      <Image
                        className="w-100"
                        src={`/assets/images/home-1/${img}`}
                        alt=""
                        width={400}
                        height={400}
                        style={{ width: '100%', height: 'auto' }}
                      />
                      <div className="gallery__thumb-icon">
                        <Image
                          src="/assets/images/home-1/gallery-icon.png"
                          alt="icon"
                          width={40}
                          height={40}
                        />
                      </div>
                    </a>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
}

export default GalleryArea;